  overflow: auto;
}

._mediaLibrary--lib--grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
  gap: 2px;
  padding: 2px;
}

._mediaLibrary--focusPane {
  // background: var(--c-bleu);
}

._focusBtns {
  position: absolute;
  top: 0;
  right: 0;
}

._topSection {
  display: flex;
  align-items: center;
  gap: calc(var(--spacing) / 2);
  margin: calc(var(--spacing) / 2);
}
